@font-face {
   font-family: 'Chillax-Variable';
   src: url('../fonts/chillax/Chillax-Variable.woff2') format('woff2'),
        url('../fonts/chillax/Chillax-Variable.woff') format('woff'),
        url('../fonts/chillax/Chillax-Variable.ttf') format('truetype');
        font-weight: 200 700;
        font-display: swap;
        font-style: normal;
 }
 
 
